
.switch {
  font-size: 10px;
  position: relative;
  display: inline-block;
  width: 3.5em;
  height: 2em;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--p6);
  border: 1px solid #CCC2FE;
  transition: .4s;
  border-radius: 30px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 1.4em;
  width: 1.4em;
  border-radius: 20px;
  left: 0.27em;
  bottom: 0.25em;
  background-color: #CCC2FE;
  transition: .4s;
}

input:checked + .slider {
  background-color: #CCC2FE;
  border: 1px solid #CCC2FE00;
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--p6);
}

input:checked + .slider:before {
  transform: translateX(1.4em);
  background-color: var(--p6);
}